@font-face {
    font-family: "gg";
    src: url(../../assets/Fonts/Gotham-Rounded-Book.ttf)
    }
    @font-face {
        font-family: "gg-light";
        src: url(../../assets/Fonts/Gotham-Rounded-Light.ttf)
        }

        @font-face {
            font-family: "gg-medium";
            src: url(../../assets/Fonts/Gotham-Rounded-Medium.ttf)
            }

.Home{
    width:100%;
    height:100vh;
    
    position:relative;
    font-family: "gg";
    
    
}
.Center{
    position: absolute;
    left:50%;
    top:50%;
    -webkit-transform: translate(-60%,-50%);
        -ms-transform: translate(-60%,-50%);
            transform: translate(-60%,-50%);
    display: inline-block;
    
    /* border:1px #000 solid; */
    
}
.Head{
    width: 100%;
    position: relative;
    left:50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    text-align: center;
 
    
}
.Image{

    display: inline-block;
    margin: 5px;
    width:70px;
    height: auto;
    
}
.ImageContent{
    -o-object-fit: cover;
       object-fit: cover;
}
.Content{

    display: inline-block;
    padding: 10px;
}
.Button{
    font-family: "gg-light";

    position: relative;
    top:50%; 
    left:50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
  width: 240px;

  height: 60px;
  font-size: 1.4rem;
  border-radius: 100px; 
  text-decoration: none;
  text-align: center;
  background: none;
  color: white;
  background: -webkit-gradient(linear, left bottom, left top, from(#0C959B) , to(#04ABA3));
  background: -webkit-linear-gradient(bottom, #0C959B , #04ABA3);
  background: -o-linear-gradient(bottom, #0C959B , #04ABA3);
  background: linear-gradient(to top, #0C959B , #04ABA3);
  border: 1px solid #0C959B;
  border-radius: 10px;
  /* overflow: hidden; */
  -webkit-box-shadow:#1C5858 0px 4px;
          box-shadow:#1C5858 0px 4px; 
  
 

}

.Button:focus{
    outline:none;
    -webkit-box-shadow: none;
            box-shadow: none;
    
}

@media only screen and (max-width:900px){
    .Image{
        display: none;
    }
    .Content,.Head,.Button{
        width:100%;
    }
    .Center{
        -webkit-transform: translate(-50%,-50%);
            -ms-transform: translate(-50%,-50%);
                transform: translate(-50%,-50%);
    }
}




